.arena-bar-team {
  /* background: ; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  /* border-left: 1px solid rgba(0, 0, 0, 0.1); */
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  background: #fff;
}

.arena-bar-team__rank {
  width: 48px;
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  background: #1d181d;
  color: #fff;
  font-size: 20px;
  font-weight: 700;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.arena-bar-team__rank.first {
  background: #fdc823;
}
.arena-bar-team__rank.second {
  background: #dcdee2;
}
.arena-bar-team__rank.third {
  background: #f0a969;
}

.arena-bar-team__rank__number {
  font-size: 26px;
}
.arena-bar-team__rank__ordinal {
  font-size: 14px;
  -webkit-transform: translateY(-5px);
      -ms-transform: translateY(-5px);
          transform: translateY(-5px);
}

.arena-bar-team__name {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;

  /* justify-content: flex-start; */
  padding: 0 5px;
  font-weight: 600;
  font-size: 16px;
  line-height: 1;
  overflow: hidden;
}

.arena-bar-team__name--inner {
  /* width: 100%; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  white-space: nowrap;
  max-width: 180px;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  height: 100%;
}

.arena-bar-team__name--item {
  display: block;
  max-width: 100%;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
  margin: auto 0;
  /* vertical-align: bottom; */
}

.arena-bar-team__name--seperator {
  display: inline-block;
  margin: 0 5px;
  opacity: 0.6;
}

.arena-bar-team__avatar {
  position: relative;
  width: 48px;
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
}

.arena-bar-team__avatar--img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.arena-bar-team__score {
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  width: 48px;
  color: #000;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-size: 24px;
  font-weight: 800;
}

.avatars-2 .arena-bar-team__avatar--img:last-child {
  -webkit-clip-path: polygon(100% 0, 0 100%, 100% 100%);
          clip-path: polygon(100% 0, 0 100%, 100% 100%);
}

.avatars-3 .arena-bar-team__avatar--img:nth-child(2) {
  -webkit-clip-path: polygon(50% 0, 0 100%, 100% 100%);
          clip-path: polygon(50% 0, 0 100%, 100% 100%);
}

.avatars-3 .arena-bar-team__avatar--img:nth-child(3) {
  -webkit-clip-path: polygon(50% 0, 0 100%, 0 0);
          clip-path: polygon(50% 0, 0 100%, 0 0);
}



